import * as React from 'react';
import { HeadFC, PageProps } from 'gatsby';
import { MainTemplate } from 'components/templates/main-template';
import { Navbar } from 'components/organisms/navbar/navbar';
import { HomePageContent } from 'components/organisms/home-page-content/home-page-content';
import { Footer } from 'components/organisms/footer/footer';

const IndexPage = ({
  location,
}: PageProps<unknown, unknown, { buildModal: boolean }>) => {
  const { state } = location;
  const { buildModal = false } = state || {};
  return (
    <MainTemplate
      header={<Navbar />}
      content={<HomePageContent buildModal={buildModal} />}
      footer={<Footer />}
    />
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;
