import React, { useCallback, useEffect, useState } from 'react';
import * as RadixSwitch from '@radix-ui/react-switch';
import classNames from 'classnames';

export interface Option<T> {
  value: T,
  label: string,
}

export type Options<T> = {
  option1: Option<T>,
  option2: Option<T>,
}

interface SwitchProps<T> {
  value: T;
  onChanged?: (value: T) => void;
  options: Options<T>;
}

export const Switch = <T, >(props: SwitchProps<T>) => {
  const { value, onChanged, options } = props;
  const { option1, option2 } = options;
  const [internalChecked, setInternalChecked] = useState(
    option2.value === value,
  );

  useEffect(() => {
    setInternalChecked(value === option2.value);
  }, [value]);

  const handleCheckedChange = useCallback((checked: boolean) => {
    if (onChanged) onChanged(checked ? option2.value : option1.value);
  }, []);

  return (
    <RadixSwitch.Root
      className={classNames(
        'h-12',
        'w-56',
        'relative',
        'rounded-[20px]',
        'border',
        'p-1',
        'border-gradient',
        'bg-theme-gradient-background-dark',
        'text-main-white',
      )}
      checked={internalChecked}
      onCheckedChange={handleCheckedChange}
    >
      <RadixSwitch.Thumb
        className={classNames(
          'bg-main-purple',
          'h-full',
          'w-1/2',
          'data-[state=checked]:translate-x-full',
          'transition-all',
          'rounded-2xl',
          'flex',
          'items-center',
          'justify-center',
          'font-extrabold',
          'relative',
          'z-20',
        )}
      >
        {internalChecked ? option2.label : option1.label}
      </RadixSwitch.Thumb>
      <div className="absolute top-0 left-0 flex h-full w-1/2 items-center justify-center font-extrabold opacity-30">
        {option1.label}
      </div>
      <div className="absolute top-0 left-1/2 flex h-full w-1/2 items-center justify-center font-extrabold opacity-30">
        {option2.label}
      </div>
    </RadixSwitch.Root>
  // </div>
  );
};
