/* eslint-disable react/no-unused-prop-types */
import { Processor } from 'domain/equipment/processor';
import React, { useEffect, useMemo, useState } from 'react';
import { between } from 'utils/between';
import { Counter } from '../../molecues/counter/counter';
import { Switch } from '../../molecues/switch/switch';

export interface BudgetProcessorCombination {
  processor: Processor,
  budgetCategory: string,
}
interface BudgetProcessorControlsProps {
  availableBudgetAmounts: {
    [Processor.AMD]: readonly string[],
    [Processor.INTEL]: readonly string[],
  },
  budgetProcessorCombination: BudgetProcessorCombination,
  onChange: (newCombination: BudgetProcessorCombination) => void,
}

const processorOptions = {
  option1: { value: Processor.INTEL, label: 'INTEL' },
  option2: { value: Processor.AMD, label: 'AMD' },
};

export const BudgetProcessorControls = ({
  availableBudgetAmounts, budgetProcessorCombination, onChange,
}: BudgetProcessorControlsProps) => {
  const {
    processor: initialProcessor,
    budgetCategory: initialBudgetCategory,
  } = budgetProcessorCombination;
  const [processor, setProcessor] = useState(initialProcessor);

  const options = useMemo(() => (
    availableBudgetAmounts[processor]
  ), [processor]);

  const [index, setIndex] = useState(
    options.findIndex((option) => option === initialBudgetCategory),
  );

  const onProcessorChange = (processor: Processor) => {
    setProcessor(processor);
  };

  const onBudgetIndexChange = (index: number) => {
    setIndex(between(0, options.length - 1, index));
  };

  useEffect(() => {
    const tempIndex = between(0, options.length - 1, index);
    const tempBudget = options[tempIndex];
    setIndex(tempIndex);
    onChange({
      processor,
      budgetCategory: tempBudget,
    });
  }, [options, index]);

  return (
    <div className="col-span-1 flex flex-col gap-6">
      <div className="flex flex-col items-center gap-2">
        <span className="font-uni-sans uppercase">procesor</span>
        <Switch
          value={processor}
          options={processorOptions}
          onChanged={onProcessorChange}
        />
        <span className="text-xs underline">jakie są różnice?</span>
      </div>
      <div className="flex flex-col items-center gap-2">
        <span className="font-uni-sans uppercase">budżet</span>
        <Counter
          value={index}
          label={options[index]}
          counterChange={1}
          onChange={onBudgetIndexChange}
          max={options.length - 1}
          min={0}
        />
      </div>
    </div>
  );
};
