import { BudgetProcessorCombination, BudgetProcessorControls } from 'components/organisms/budget-processor-controls/budget-processor-controls';
import { PrimaryButton } from 'components/molecues/button';
import { CloseButton } from 'components/molecues/close-button/close-button';
import { Dialog } from 'components/molecues/dialog/dialog';
import { Processor } from 'domain/equipment/processor';
import { Resolution } from 'domain/equipment/resolution';
import { graphql, Link, useStaticQuery } from 'gatsby';
import React, { useMemo, useState } from 'react';
import { ResolutionToggleGroup } from 'components/molecues/resolution-toggle-group/resolution-toggle-group';
import { GamesFpsGallery } from '../games-fps-gallery/games-fps-gallery';
import { useGalleryData } from '../games-fps-gallery/static-query-hook';

interface BudgetProcessorConfigurationProps {
  onClose?: () => void;
}

const BudgetProcessorConfiguration = ({ onClose }: BudgetProcessorConfigurationProps) => {
  const [resolution, setResolution] = useState(Resolution.FHD);
  const { amd: { distinct: amdCategories }, intel: { distinct: intelCategories } } = useStaticQuery<Queries.ProcessorBudgetCategoryQuery>(graphql`
    query ProcessorBudgetCategory {
      amd: allEquipmentSetsJson(
        sort: {priceCategory: ASC}
        filter: {platform: {eq: "amd"}}
      ) {
        distinct(field: {priceCategory: SELECT})
      }
      intel: allEquipmentSetsJson(
        sort: {priceCategory: ASC}, 
        filter: {platform: {eq: "intel"}}
      ) {
        distinct(field: {priceCategory: SELECT})
      }
    }
`);

  const availableBudgetAmounts = useMemo(() => ({
    [Processor.AMD]: amdCategories,
    [Processor.INTEL]: intelCategories,
  }), []);

  const [
    budgetProcessorCombination,
    setBudgetProcessorCombination,
  ] = useState<BudgetProcessorCombination>({
    processor: Processor.INTEL,
    budgetCategory: availableBudgetAmounts[Processor.INTEL][0],
  });

  const data = useGalleryData();

  const fpsData = useMemo(
    () => data.filter((item) => (
      item.processor === budgetProcessorCombination.processor
      && item.priceCategory === budgetProcessorCombination.budgetCategory
      && item.resolution === resolution
    )).slice(0, 4),
    [resolution, budgetProcessorCombination],
  );

  return (
    <>
      <div className="hidden h-fit w-[920px] animate-fadeIn flex-col rounded-3xl bg-background-light p-6 text-main-white md:flex">
        <div className="p-6">
          <div className="flex justify-between">
            <h1 className="font-uni-sans text-3xl uppercase">zbuduj pc</h1>
            <CloseButton onClick={onClose} className="h-4" />
          </div>
          <h2 className="font-raleway text-lg">
            Określ budżet i preferowany procesor
          </h2>
        </div>
        <div className="grid h-full grid-cols-3 gap-6 p-6">
          <div className="col-span-1 mt-6 flex flex-col gap-6">
            <BudgetProcessorControls
              budgetProcessorCombination={budgetProcessorCombination}
              onChange={setBudgetProcessorCombination}
              availableBudgetAmounts={availableBudgetAmounts}
            />
          </div>
          <div className="col-span-2">
            <div className="h-72 rounded-2xl bg-background-dark">
              <div className="flex h-full flex-col p-6">
                <div className="flex justify-between ">
                  <span className="whitespace-nowrap text-base font-light">
                    Zobacz szacowaną wydajność
                  </span>
                  <div className="flex justify-end">
                    <ResolutionToggleGroup value={resolution} onChange={setResolution} className="bg-theme-gradient-background-dark" />
                  </div>
                </div>
                <GamesFpsGallery
                  fpsData={fpsData}
                  className="md:grid-cols-4"
                />
              </div>
            </div>
          </div>
        </div>
        <div className="flex h-fit justify-end p-6">
          <Link to={`/equipment-set/${budgetProcessorCombination.processor}/${budgetProcessorCombination.budgetCategory}/`}>
            <PrimaryButton>Generuj bebechy</PrimaryButton>
          </Link>
        </div>
      </div>
      <div className="relative z-10 flex h-full flex-col gap-4 px-6 text-main-white md:hidden">
        <div className="sticky top-0 left-0 z-20 h-fit bg-top-shadow pt-12 pb-8">
          <div className="flex justify-end">
            <CloseButton onClick={onClose} className="h-4" />
          </div>
          <div className="w-full text-center">
            <h1 className="font-uni-sans text-3xl uppercase">zbuduj pc</h1>
            <h2 className="font-raleway text-lg">
              Określ budżet i preferowany procesor
            </h2>
          </div>
        </div>
        <div className="z-10">
          <div className="rounded-3xl bg-background-light p-8">
            <BudgetProcessorControls
              budgetProcessorCombination={budgetProcessorCombination}
              onChange={setBudgetProcessorCombination}
              availableBudgetAmounts={availableBudgetAmounts}
            />
          </div>
          <h2 className="text-center font-raleway text-lg">
            Zobacz szacowaną wydajność
          </h2>
          <div className="flex flex-col items-center gap-6 rounded-3xl bg-background-light p-8">
            <ResolutionToggleGroup value={resolution} onChange={setResolution} className="bg-theme-gradient-background-light" />
            <GamesFpsGallery
              className="md:grid-cols-4"
              fpsData={fpsData}
            />
          </div>
        </div>
        <div className="sticky bottom-0 left-0 z-20 flex h-48 w-full justify-end bg-bottom-shadow pt-10 pb-32">
          <Link to={`/equipment-set/${budgetProcessorCombination.processor}/${budgetProcessorCombination.budgetCategory}/`} className="w-full">
            <PrimaryButton className="w-full">Generuj bebechy</PrimaryButton>
          </Link>
        </div>
      </div>
    </>
  );
};

interface BudgetProcessorPopupProps {
  isOpen: boolean;
  onClose: () => void;
}

const BudgetProcessorPopup = ({
  isOpen,
  onClose,
}: BudgetProcessorPopupProps) => (
  <Dialog open={isOpen} className="h-full w-full" overlayClassName="opacity-100">
    <BudgetProcessorConfiguration onClose={onClose} />
  </Dialog>
);

export { BudgetProcessorPopup };
