/* eslint-disable no-underscore-dangle */
import { Processor } from 'domain/equipment/processor';
import { useStaticQuery, graphql } from 'gatsby';
import { convertToGalleryData, PerformanceQuery } from 'infrastructure/equipment/convert-to-gallery-data';
import { useMemo } from 'react';

export const useGalleryData = () => {
  const { allEquipmentSetsJson: { edges: nodes } } = useStaticQuery<Queries.GamesPerformancesQuery>(graphql`
  query GamesPerformances {
    allEquipmentSetsJson {
    edges {
      node {
        performance {
          _1080p {
            name
            imgFile {
              childImageSharp {
                gatsbyImageData
              }
            }
            fps
          }
          _1440p {
            name
            fps
            imgFile {
              childImageSharp {
                gatsbyImageData
              }
            }
          }
          _4k {
            name
            fps
            imgFile {
              childImageSharp {
                gatsbyImageData
              }
            }
          }
        }
        priceCategoryString
        platform
      }
    }
  }
  }
  `);

  const data = useMemo(() => nodes.flatMap((node) => (convertToGalleryData(
    (node.node.performance || {}) as PerformanceQuery,
    node.node.platform as Processor,
    node.node.priceCategoryString || '',
  ))), [nodes]);

  return data;
};
