/* eslint-disable react/jsx-props-no-spreading */
import classNames from 'classnames';
import { Icon } from 'components/atoms/icon/icon';
import React, {
  useCallback, useId,
} from 'react';
import { ThemeSvgGradient } from '../theme-svg-gradient/theme-svg-gradient';

const buttonPaths = {
  left: 'M1 23C1 10.574 11.074.5 23.5.5h15A7.5 7.5 0 0 1 46 8v30a7.5 7.5 0 0 1-7.5 7.5h-15C11.074 45.5 1 35.426 1 23Z',
  right: 'M1 8C1 3.85787 4.35786 0.5 8.5 0.5H23.5C35.9264 0.5 46 10.5736 46 23C46 35.4264 35.9264 45.5 23.5 45.5H8.5C4.35787 45.5 1 42.1421 1 38V8Z',
};

const CounterButton = (path: string) => (
  // eslint-disable-next-line react/prop-types
  ({ children, className, ...restProps }: React.HTMLAttributes<HTMLButtonElement>) => {
    const id = useId();
    return (
      <button type="button" {...restProps} className={classNames(className, 'h-full')}>
        <svg
          viewBox="0 0 47 46"
          xmlns="http://www.w3.org/2000/svg"
          className={classNames(
            'h-full',
            'min-w-fit',
            'fill-background-light',
          )}
        >
          <path d={path} stroke={`url(#${id})`} />
          <foreignObject height="100%" width="100%">
            <div className="flex h-full items-center justify-center">
              {children}
            </div>
          </foreignObject>
          <ThemeSvgGradient id={id} />
        </svg>
      </button>
    );
  }
);

const LeftButton = CounterButton(buttonPaths.left);
const RightButton = CounterButton(buttonPaths.right);

interface CounterProps {
  className?: string,
  value: number,
  counterChange: number,
  min?: number,
  max?: number,
  onChange?: (value: number) => void;
  label: string | number,
}

export const Counter = (props: CounterProps) => {
  const {
    className,
    value,
    counterChange = 1,
    min = 0,
    max,
    onChange,
    label,
  } = props;

  const increment = useCallback(() => {
    const newValue = value + counterChange;
    if (onChange) onChange(max === undefined || (newValue <= max) ? newValue : value);
  }, [value, max]);

  const decrement = useCallback(() => {
    const newValue = value - counterChange;
    if (onChange) onChange(min === undefined || (newValue >= min) ? newValue : value);
  }, [value, min]);

  return (
    <div className={classNames('flex', 'h-12', 'w-full', 'items-center', className)}>
      <LeftButton className="translate-x-2" onClick={decrement}>
        <Icon name="ArrowLeft" />
      </LeftButton>
      <div className="flex h-full w-full items-center justify-center overflow-hidden  bg-background-dark px-6">
        <span className=" bg-background-dark font-raleway text-sm font-extrabold text-main-white">{label}</span>
      </div>
      <RightButton className="-translate-x-2" onClick={increment}>
        <Icon name="ArrowRight" />
      </RightButton>
    </div>
  );
};
